/*$purple-3: #7c4dff;*/
.userSocialMedias {
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  width: 100%;
  padding: 0;
  margin: 0;
}
.userSocialMedias button {
  width: 60px;
  height: 64px;
  border-radius: 100%;
  padding: 10px;
  margin: 0 6px 10px 6px;
}
.userSocialMedias button img {
  height: 30px;
  width: 30px;
}
.userSocialMedias button.active {
  background-color: #7159c1;
}
.userSocialMedias button.active img {
  -webkit-filter: saturate(0) brightness(4);
          filter: saturate(0) brightness(4);
}

@media only screen and (max-width: 992px) {
  .userSocialMedias {
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
    -ms-flex-pack: justify;
        justify-content: space-between;
  }
  .userSocialMedias .button {
    width: 40px;
    height: 40px;
  }
  .userSocialMedias .button img {
    height: 20px;
    width: 20px;
  }
}



