/*$purple-3: #7c4dff;*/
.discussion .filters {
  -ms-flex-direction: row;
      flex-direction: row;
  padding-bottom: 0;
}
.discussion .filters .comboBox {
  margin-right: 20px;
}
.discussion section.posts {
  -ms-flex-direction: row;
      flex-direction: row;
  padding-top: 20px;
}
.discussion section.posts > div {
  margin: 0 10px;
}
.discussion section.posts .postCards {
  -ms-flex: 7 1;
      flex: 7 1;
}
.discussion section.posts .optionsColumn {
  -ms-flex: 3 1;
      flex: 3 1;
}
.discussion section.posts .optionsColumn button {
  width: 100%;
}
.discussion section.posts .optionsColumn div {
  width: 100%;
  height: 200px;
  border: 1px solid #7159c1;
  margin: 20px 0;
  cursor: pointer;
}

@-webkit-keyframes load {
  0% {
    background-color: #7159c1;
  }
  50% {
    background-color: #6666ff;
  }
  100% {
    background-color: #7159c1;
  }
}

@keyframes load {
  0% {
    background-color: #7159c1;
  }
  50% {
    background-color: #6666ff;
  }
  100% {
    background-color: #7159c1;
  }
}



