/*$purple-3: #7c4dff;*/
.home h2 {
  margin-bottom: 40px;
  font-size: 2.2rem;
  font-weight: bold;
  -ms-flex-item-align: center;
      align-self: center;
  text-align: center;
}
.home section > span {
  color: #4c4c4c;
  font-size: 0.9rem;
  margin: 0 auto;
  margin-bottom: 3px;
  margin-top: 20px;
  text-align: center;
}
.home section.intro {
  position: relative;
  -ms-flex-direction: row;
      flex-direction: row;
  background-color: #7159c1;
  padding-bottom: 0px;
  margin-bottom: 80px;
}
.home section.intro .ellipse {
  margin: 0;
  background-color: #7159c1;
  z-index: -1;
  width: 120%;
  display: block;
  height: 170px;
  position: absolute;
  left: -10%;
  bottom: -60px;
  border-bottom-left-radius: 100%;
  border-bottom-right-radius: 100%;
}
.home section.intro div {
  -ms-flex: 3 1;
      flex: 3 1;
  margin: 0 50px;
  color: white;
  -ms-flex-item-align: center;
      align-self: center;
}
.home section.intro div h2 {
  text-align: left;
  margin-bottom: 20px;
}
.home section.intro div p {
  font-size: 1.2rem;
}
.home section.intro div img {
  height: 100%;
  mix-blend-mode: hard-light;
  -o-object-fit: contain;
     object-fit: contain;
  width: 100%;
  max-width: 250px;
}
.home section.intro div:nth-child(2) {
  -ms-flex: 2 1;
      flex: 2 1;
}
.home section.headline > div > div {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  margin: 0 10px;
  font-size: 1.2rem;
}
.home section.headline > div > div p {
  margin: 0;
}
.home section.headline > div > div p:not(:last-child) {
  margin-bottom: 1em;
}
.home section.headline > div > div:nth-child(1) {
  -ms-flex: 3 1;
      flex: 3 1;
}
.home section.headline > div > div:nth-child(2) {
  -ms-flex: 2 1;
      flex: 2 1;
}
.home section.headline > div > div:nth-child(2) img {
  width: 100%;
  max-width: 600px;
  border-radius: 30px;
}
.home section.features {
  background-color: #fafafa;
}
.home section.problem div.data {
  padding: 0 10px;
}
.home section.problem div.data div {
  margin-bottom: 0;
}
.home section.problem div.data img {
  padding: 15px;
  border: 1px solid #5758D5;
  border-radius: 100%;
  height: 150px;
  width: 150px;
  -ms-flex-item-align: center;
      align-self: center;
  margin: 0 auto;
}
.home section.problem div.data div {
  margin-left: 20px;
}
.home section.problem div.data div p {
  font-size: 1.4rem;
  margin: 0;
}
.home section.problem div.data div p:first-child {
  margin-bottom: 20px;
}
.home section.problem div.data div p span {
  display: block;
  font-size: 2.5rem;
  line-height: 2.5rem;
  font-weight: bold;
  color: #5758D5;
  text-align: left;
}
.home section.problem div.explanation {
  display: -ms-inline-flexbox;
  display: inline-flex;
  margin-top: 40px;
}
.home section.problem div.explanation div {
  -ms-flex: 1 1;
      flex: 1 1;
  padding: 0 10px;
}
.home section.problem div.explanation div h3 {
  color: #5758D5;
  text-align: left;
}
.home section.problem div.explanation div h3::after {
  margin-bottom: 12px;
  background-color: #5758D5;
}
.home section.problem div.explanation div p {
  margin: 0;
}
.home section.price {
  background-color: #7159c1;
  color: white;
}
.home section.price > span {
  color: white;
  font-size: 1.4rem;
  margin-top: 0;
  margin-bottom: 10px;
}

@media only screen and (max-width: 768px) {
  .home section.intro blockquote {
    margin: 0 15%;
  }
  .home .problem div.data img {
    padding: 15px;
    height: 100px;
    width: 100px;
  }
}



