/*$purple-3: #7c4dff;*/
.login section, .signup section, .contact section {
  min-height: calc(100vh - 190px);
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-direction: row;
      flex-direction: row;
}
.login section > div, .signup section > div, .contact section > div {
  -ms-flex: 1 1;
      flex: 1 1;
}
.login section > div:first-child, .signup section > div:first-child, .contact section > div:first-child {
  margin: 0 calc(100% / 6 + 40px) 0 10px;
}
.login section div:not(.userSocialMedias) form > a, .signup section div:not(.userSocialMedias) form > a, .contact section div:not(.userSocialMedias) form > a {
  width: 100%;
}
.login h1, .signup h1, .contact h1 {
  font-size: 2.2rem;
}
.login h1::after, .signup h1::after, .contact h1::after {
  margin-bottom: 40px;
}

@media only screen and (max-width: 992px) {
  .login section > div:first-child, .signup section > div:first-child, .contact section > div:first-child {
    margin-right: 20px;
  }
}
@media only screen and (max-width: 768px) {
  .login section > div, .signup section > div, .contact section > div {
    -ms-flex-preferred-size: 100%;
        flex-basis: 100%;
  }
  .login section > div:first-child, .signup section > div:first-child, .contact section > div:first-child {
    margin-bottom: 60px;
  }
}



