/*$purple-3: #7c4dff;*/
.settings section {
  -ms-flex-align: center;
      align-items: center;
  width: 450px;
  -webkit-box-sizing: content-box;
          box-sizing: content-box;
  margin: 0 auto;
  padding-top: 20px;
}
.settings h1 {
  font-size: 2.2rem;
  font-weight: bold;
  -ms-flex-item-align: center;
      align-self: center;
}
.settings h1::after {
  margin-bottom: 40px;
}
.settings .avatar {
  position: relative;
  width: 250px;
  height: 250px;
  border-radius: 100%;
  background-color: #7159c1;
}
.settings .avatar img {
  height: inherit;
  padding: inherit;
}
.settings .avatar > img {
  border-radius: 100%;
}
.settings .avatar > div {
  position: absolute;
  right: 15px;
  bottom: 0;
  border-radius: 100%;
  background-color: white;
}
.settings .avatar > div button {
  width: 60px;
  height: 60px;
  border: 2px solid #7159c1;
}
.settings .userSocialMedias {
  margin-bottom: 20px;
}
.settings .options {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
}
.settings .options button, .settings .options a {
  -ms-flex: 1 1;
      flex: 1 1;
}
.settings .options button:last-child, .settings .options a:last-child {
  margin-left: 20px;
}



