/*$purple-3: #7c4dff;*/
.dashboard h2 {
  font-size: 1.8rem;
}
.dashboard h2::after {
  margin-bottom: 0;
}
.dashboard section.userData {
  padding-bottom: 0;
}
.dashboard section.userData h1 {
  font-size: 2.2rem;
  font-weight: bold;
  -ms-flex-item-align: center;
      align-self: center;
  margin-bottom: 20px;
}
.dashboard section.userData h1::after {
  content: none;
}
.dashboard section.userData > div {
  -ms-flex-line-pack: center;
      align-content: center;
}
.dashboard section.userData > div span {
  display: block;
  color: #4c4c4c;
  text-align: center;
}
.dashboard section.userData .level {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  width: 35%;
  margin: 0 auto;
  margin-top: 10px;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.dashboard section.userData .level span {
  position: absolute;
  width: 100%;
  height: 3px;
  margin: 0 auto;
  background-color: #7159c1;
  -ms-flex-item-align: center;
      align-self: center;
}
.dashboard section.userData .level div {
  width: 20px;
  height: 20px;
  border-radius: 100%;
  background-color: #7159c1;
  z-index: 2;
}
.dashboard section.userData .level div.active {
  border: 5px solid #7159c1;
  background-color: white;
}
.dashboard section.objectives {
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-direction: row;
      flex-direction: row;
  padding-top: 40px;
}
.dashboard section.objectives > div {
  -ms-flex: 1 1;
      flex: 1 1;
}
.dashboard section.objectives > div:first-child div:not(.courses) {
  padding: 0 10px;
}
.dashboard section.objectives > div:first-child .courses {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  margin-bottom: 1rem;
}
.dashboard section.objectives > div:first-child .courses button, .dashboard section.objectives > div:first-child .courses a {
  text-align: center;
  margin: 5px 10px;
}
.dashboard section.objectives > div:last-child {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  padding: 0 10px;
}
.dashboard section.objectives > div:last-child .post {
  border: 1px solid rgba(151, 151, 151, 0.2);
  color: white;
  margin-bottom: 20px;
  padding: 15px;
  background-color: #7159c1;
  -webkit-transition: -webkit-box-shadow 0.5s;
  transition: -webkit-box-shadow 0.5s;
  -o-transition: box-shadow 0.5s;
  transition: box-shadow 0.5s;
  transition: box-shadow 0.5s, -webkit-box-shadow 0.5s;
  cursor: pointer;
}
.dashboard section.objectives > div:last-child .post > div {
  display: -ms-inline-flexbox;
  display: inline-flex;
  margin-bottom: 20px;
}
.dashboard section.objectives > div:last-child .post > div > div {
  -ms-flex: 3 1;
      flex: 3 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  margin: 0 0 0 10px;
}
.dashboard section.objectives > div:last-child .post > div > div h3::after {
  background-color: white;
  margin-bottom: 10px;
}
.dashboard section.objectives > div:last-child .post > div > div p {
  margin: 0;
}
.dashboard section.objectives > div:last-child .post > div > div:first-child {
  -ms-flex: 1 1;
      flex: 1 1;
  margin: 0 10px 0 0;
}
.dashboard section.objectives > div:last-child .post > div > div:first-child img {
  width: 100%;
  border-radius: 100%;
  min-width: 80px;
}

@media screen and (max-width: 768px) {
  .dashboard section.userData .level {
    width: 60%;
  }
}



