/*$purple-3: #7c4dff;*/
.about h2 {
  color: #5758D5;
  margin-left: 10px;
}
.about h2::after {
  background-color: #5758D5;
}
.about section.problem h1 {
  -ms-flex-item-align: center;
      align-self: center;
  text-align: center;
  font-size: 2.2rem;
}
.about section.problem h1::after {
  margin-bottom: 40px;
}
.about section.problem h2::after {
  margin-bottom: 20px;
}
.about section.problem > div {
  display: -ms-inline-flexbox;
  display: inline-flex;
}
.about section.problem > div > div {
  padding: 0 10px;
}
.about section.problem > div > div img {
  width: 100%;
}
.about section.problem > div > div p:first-child {
  margin-top: 0;
}
.about section.differentials {
  background-color: #fafafa;
}
.about section.differentials .differential:not(:last-child) {
  margin-bottom: 30px;
}
.about section.aboutUs {
  background-color: #7159c1;
}
.about section.aboutUs > h2 {
  color: white;
}
.about section.aboutUs > h2::after {
  background-color: white;
  margin-bottom: 40px;
}
.about section.aboutUs > div {
  display: -ms-inline-flexbox;
  display: inline-flex;
}



