/*$purple-3: #7c4dff;*/
.userHeader {
  position: relative;
  display: -ms-inline-flexbox;
  display: inline-flex;
  background-color: #7159c1;
  height: 80px;
  width: 100%;
  padding: 10px 10px 10px calc(15% + 10px);
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.userHeader > a {
  height: 100%;
}
.userHeader img {
  height: 60px;
}
.userHeader > .nav {
  display: -ms-inline-flexbox;
  display: inline-flex;
  height: 100%;
}
.userHeader > .nav .name {
  color: white;
  font-size: 2rem;
  margin: 0 10px 0 20px;
  -ms-flex-item-align: center;
      align-self: center;
}
.userHeader > .nav div {
  position: relative;
}
.userHeader > .nav div:first-child {
  -ms-flex-item-align: center;
      align-self: center;
}
.userHeader > .nav div:first-child .material-icons {
  font-size: 2rem;
}
.userHeader > .nav div:not(:first-child) img {
  border-radius: 100%;
  padding: 6px 0;
}
.userHeader > .nav div:not(:first-child) button {
  position: absolute;
  right: -5px;
  bottom: 0;
  width: 25px;
  height: 25px;
  border: 1px solid white;
  background-color: rgba(113, 89, 193, 0.85);
}
.userHeader > .nav div:not(:first-child) button .material-icons {
  font-size: 16px;
}
.userHeader .slideMenu {
  display: none;
}
.userHeader .slideMenu .menu-button {
  position: absolute;
  top: calc(50% - 20px / 2);
  left: 20px;
  -ms-flex-item-align: center;
      align-self: center;
  height: 20px;
  width: 26px;
}
.userHeader .slideMenu .menu-button span {
  background-color: white;
  -webkit-transition: background-color 0.4s;
  -o-transition: background-color 0.4s;
  transition: background-color 0.4s;
}
.userHeader .slideMenu .menu-button:hover span {
  background-color: #c0c0c0;
}

@media screen and (max-width: 768px) {
  .userHeader {
    height: 60px;
    padding: 10px 5%;
  }
  .userHeader a {
    margin: 0 auto;
  }
  .userHeader a img {
    height: 40px;
  }
  .userHeader > .nav {
    display: none;
  }
  .userHeader .slideMenu {
    display: block;
  }
}



