.post section {
  -ms-flex-direction: row;
      flex-direction: row;
}
.post section > div {
  margin: 0 10px;
}
.post section .messages {
  -ms-flex: 7 1;
      flex: 7 1;
}
.post section .messages > h1 {
  margin-top: 40px;
  font-size: 2.2rem;
}
.post section .messages .answers .message {
  margin-bottom: 40px;
}
.post section .optionsColumn {
  -ms-flex: 3 1;
      flex: 3 1;
}
.post section .optionsColumn a {
  width: 100%;
}
.post section .optionsColumn div {
  width: 100%;
  height: 200px;
  border: 1px solid #7159c1;
  margin: 20px 0;
  cursor: pointer;
}



