.footer {
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  width: 100%;
  height: 90px;
  padding: 0 calc(15% + 10px);
}
.footer p {
  margin: 0;
}
.footer.footer-filled {
  color: white;
  background-color: #7159c1;
}

@media only screen and (max-width: 768px) {
  .footer {
    padding: 0 5%;
  }
}



