/*$purple-3: #7c4dff;*/
.message {
  display: -ms-inline-flexbox;
  display: inline-flex;
}
.message .userProfile {
  -ms-flex: 2 1;
      flex: 2 1;
  margin-right: 10px;
}
.message .userProfile .avatar {
  position: relative;
  width: 100%;
  height: auto;
}
.message .userProfile .avatar img {
  width: 100%;
  height: inherit;
  padding: inherit;
  border-radius: 100%;
}
.message .userProfile .avatar span {
  display: -ms-flexbox;
  display: flex;
  position: absolute;
  right: 10px;
  bottom: 8px;
  width: 40px;
  height: 40px;
  border-radius: 100%;
  background-color: rgba(255, 255, 255, 0.9);
  color: #7159c1;
  border: 1px solid #7159c1;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  font-weight: bold;
}
.message .userProfile .avatar::after {
  content: "";
  display: block;
  width: 100%;
  height: 3px;
  margin-top: 8px;
  background-color: #252525;
}
.message .userProfile p {
  text-align: center;
  margin-top: 2px;
  margin-bottom: 0px;
}
.message .userProfile p.username {
  font-weight: bold;
  margin-top: 10px;
}
.message .messageData {
  -ms-flex: 8 1;
      flex: 8 1;
  margin-left: 10px;
}
.message .messageData h1 {
  font-weight: bold;
}
.message .messageData h1::after {
  content: none;
}
.message .messageData label {
  font-size: 0.9rem;
  color: #4c4c4c;
  font-style: italic;
}



