/*$purple-3: #7c4dff;*/
.feature {
  text-align: center;
  padding: 15px;
}
.feature > div {
  border: 1px solid rgba(87, 88, 213, 0.5);
  border-radius: 100%;
  padding: 25px;
  height: 140px;
  width: 140px;
  margin: 0 auto;
  -webkit-transition: border-color 0.25s, background-color 0.25s;
  -o-transition: border-color 0.25s, background-color 0.25s;
  transition: border-color 0.25s, background-color 0.25s;
}
.feature > div:hover {
  border-color: #5758D5;
  background-color: rgba(87, 88, 213, 0.05);
}
.feature > div img {
  height: 100%;
}
.feature h3 {
  margin-top: 20px;
  -ms-flex-item-align: center;
      align-self: center;
  font-weight: bold;
}
.feature h3::after {
  background-color: white;
  margin-bottom: 10px;
}
.feature p {
  margin: 0;
}



