/*$purple-3: #7c4dff;*/
.comboBox {
  position: relative;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  -ms-flex-align: center;
      align-items: center;
}
.comboBox label {
  color: #4c4c4c;
  margin-right: 5px;
}
.comboBox select {
  color: #4c4c4c;
  border: 1px solid #7159c1;
  border-radius: 8px;
  font-size: 1.2rem;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  min-width: 160px;
  padding: 3px;
  padding-right: 1.6rem;
  outline: none;
  background-color: transparent;
}
.comboBox img {
  position: absolute;
  height: 0.9rem;
  right: 0.45rem;
  top: calc(50% - 0.45rem);
  pointer-events: none;
}



