/*$purple-3: #7c4dff;*/
.socialMedias {
  display: -ms-inline-flexbox;
  display: inline-flex;
  margin: 0;
  margin-left: 20px;
  padding: 0;
}
.socialMedias a img {
  height: 30px;
  width: 30px;
}
.socialMedias a:not(:last-child) {
  margin: 0 20px;
}
.socialMedias a:first-child {
  margin-left: 0;
}
.socialMedias.inverse a {
  -webkit-transition: background-color 0.4s;
  -o-transition: background-color 0.4s;
  transition: background-color 0.4s;
}
.socialMedias.inverse a img {
  -webkit-filter: saturate(0) brightness(4);
          filter: saturate(0) brightness(4);
}
.socialMedias.inverse .button:hover {
  background-color: white;
}
.socialMedias.inverse .button:hover img {
  -webkit-filter: none;
          filter: none;
}

@media only screen and (max-width: 768px) {
  .socialMedias .button {
    height: 40px;
    width: 40px;
    border-radius: 100%;
    padding: 6px;
    border: 3px solid #252525;
  }
  .socialMedias .button img {
    height: 26px;
    width: 26px;
  }
  .socialMedias .button:not(:last-child) {
    margin: 0 10px;
  }
  .socialMedias .button:first-child {
    margin-left: 0;
  }
}



