/*$purple-3: #7c4dff;*/
.slideMenu .menu-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0);
  z-index: 1000;
  pointer-events: none;
  -webkit-transition: background-color 0.4s;
  -o-transition: background-color 0.4s;
  transition: background-color 0.4s;
}
.slideMenu .menu {
  position: fixed;
  opacity: 0;
  top: 0;
  left: 0;
  height: 100vh;
  width: 70vw;
  -webkit-transform: translateX(-70vw);
      -ms-transform: translateX(-70vw);
          transform: translateX(-70vw);
  z-index: 1001;
  background-color: #fafafa;
  -webkit-box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
          box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
  -webkit-transition: opacity 0.4s 0s, -webkit-transform 0.4s;
  transition: opacity 0.4s 0s, -webkit-transform 0.4s;
  -o-transition: transform 0.4s, opacity 0.4s 0s;
  transition: transform 0.4s, opacity 0.4s 0s;
  transition: transform 0.4s, opacity 0.4s 0s, -webkit-transform 0.4s;
}
.slideMenu.open .menu-overlay, .slideMenu.open .menu {
  display: block;
}
.slideMenu.open .menu-overlay {
  pointer-events: all;
  background-color: rgba(0, 0, 0, 0.4);
}
.slideMenu.open .menu {
  opacity: 1;
  -webkit-transform: none;
      -ms-transform: none;
          transform: none;
}
.slideMenu .menu-button {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 20px;
  height: 20px;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.slideMenu .menu-button span {
  width: 100%;
  height: 2px;
  border-radius: 5px;
  background-color: black;
}



