/*$purple-3: #7c4dff;*/
.header {
  position: relative;
  display: -ms-inline-flexbox;
  display: inline-flex;
  background-color: #7159c1;
  height: 100px;
  width: 100%;
  padding: 10px 20px;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.header nav {
  -ms-flex: 1 1;
      flex: 1 1;
  -ms-flex-item-align: center;
      align-self: center;
}
.header nav > a {
  margin: 0 10px;
}
.header nav:last-child {
  text-align: right;
}
.header > a {
  font-family: "CinzelDecorative", sans-serif;
  font-size: 3rem;
  color: white;
  -ms-flex-item-align: center;
      align-self: center;
}
.header .slideMenu {
  display: none;
}
.header .slideMenu .menu-button {
  position: absolute;
  top: calc(50% - 20px / 2);
  left: 20px;
  -ms-flex-item-align: center;
      align-self: center;
  height: 20px;
  width: 26px;
}
.header .slideMenu .menu-button span {
  background-color: white;
  -webkit-transition: background-color 0.4s;
  -o-transition: background-color 0.4s;
  transition: background-color 0.4s;
}
.header .slideMenu .menu-button:hover span {
  background-color: #c0c0c0;
}

@media screen and (max-width: 768px) {
  .header {
    height: 60px;
    padding: 10px 5%;
  }
  .header a {
    margin: 0 auto;
  }
  .header a img {
    height: 40px;
  }
  .header nav {
    display: none;
  }
  .header .slideMenu {
    display: block;
  }
}



