/*$purple-3: #7c4dff;*/
.discover section.professions {
  padding-top: 40px;
  -ms-flex-align: center;
      align-items: center;
}
.discover section.professions h2 {
  font-size: 1.4rem;
  font-weight: bold;
}
.discover section.professions h2::after {
  content: none;
}
.discover section.professions .comboBox {
  margin-bottom: 10px;
}
.discover section.professions .path {
  width: 100%;
}
.discover section.professions .path .profession {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
.discover section.professions .path .profession > div:first-child {
  display: -ms-inline-flexbox;
  display: inline-flex;
}
.discover section.professions .path .profession a {
  width: 120px;
  height: 120px;
  background-color: #7159c1;
  border-radius: 100%;
  margin-top: -4px;
}
.discover section.professions .path .profession a img {
  width: 100%;
  height: 100%;
  border: 3px solid #7159c1;
  border-radius: 100%;
  -o-object-fit: cover;
     object-fit: cover;
  -webkit-transition: -webkit-filter 0.4s;
  transition: -webkit-filter 0.4s;
  -o-transition: filter 0.4s;
  transition: filter 0.4s;
  transition: filter 0.4s, -webkit-filter 0.4s;
  cursor: pointer;
}
.discover section.professions .path .profession a img:hover {
  -webkit-filter: brightness(120%);
          filter: brightness(120%);
}
.discover section.professions .path .profession:nth-child(even) div:first-child {
  width: 100%;
  height: 180px;
}
.discover section.professions .path .profession:nth-child(even) a {
  -ms-flex-item-align: end;
      align-self: flex-end;
}
.discover section.professions .path .profession:nth-child(even) > div span {
  display: block;
  height: 50%;
  width: 50%;
}
.discover section.professions .path .profession:nth-child(even) > div span:last-child {
  margin-top: 86px;
  margin-right: 58px;
  border-right: 4px solid #7159c1;
  border-top: 4px solid #7159c1;
  border-top-right-radius: 20px;
}
.discover section.professions .path .profession:nth-child(even) > div span:first-child {
  margin-left: 58px;
  border-left: 4px solid #7159c1;
  border-bottom: 4px solid #7159c1;
  border-bottom-left-radius: 20px;
}
.discover section.professions .path .profession:nth-child(odd) div:first-child {
  width: 100%;
  height: 180px;
}
.discover section.professions .path .profession:nth-child(odd) > div span {
  display: block;
  height: 50%;
  width: 50%;
}
.discover section.professions .path .profession:nth-child(odd) > div span:first-child {
  margin-top: 86px;
  margin-left: 58px;
  border-left: 4px solid #7159c1;
  border-top: 4px solid #7159c1;
  border-top-left-radius: 20px;
}
.discover section.professions .path .profession:nth-child(odd) > div span:last-child {
  margin-right: 58px;
  border-right: 4px solid #7159c1;
  border-bottom: 4px solid #7159c1;
  border-bottom-right-radius: 20px;
}
.discover section.professions .path .profession:first-child div:first-child {
  width: 50%;
  height: 180px;
}
.discover section.professions .path .profession:first-child div:first-child span:last-child {
  margin-right: 0;
}

.Popover .Popover-body {
  background-color: #7159c1;
  -webkit-box-shadow: 0 0 1px rgba(0, 0, 0, 0.4);
          box-shadow: 0 0 1px rgba(0, 0, 0, 0.4);
  padding: 15px;
  color: white;
  max-width: calc(35vw - 120px - 30px);
}
.Popover .Popover-body h3 {
  font-size: 1.4rem;
}
.Popover .Popover-body h3::after {
  background-color: white;
  margin-bottom: 0;
}
.Popover .Popover-tip {
  fill: #7159c1;
  stroke: rgba(0, 0, 0, 0.2);
  z-index: 2;
}



